                    @play="(track) => { playingTrack = track; }"
                    @stop="playingTrack = null"
                    :index="item.index"
                    :key="index"
                    :tag="item.tag"
                    category="dogs"></player-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { items } from '../../app/dogs/index';

import PlayerCard from '../../components/general/PlayerCard';

export default {
