export default class Item {
    constructor() {
        this.category = null;
        this.tag = null;
        this.index = null;
    }
    static forDogs() {
        const instance = new Item();
        instance.category = 'dogs';
        return instance;
    }
    withTag(tag) {
        this.tag = tag;
        return this;
    }
    withIndex(index) {
        this.index = index;
        return this;
    }
}