export default class Route {
    /**
     * 
     * @param {String} path 
     */
    constructor(path) {
        this.path = path;
        this.name = null;
        this.component = undefined;
    }

    /**
     * 
     * @param {String} path 
     */
    static withPath(path) {
        return new Route(path);
    }

    /**
     * 
     * @param {String} name 
     */
    hasName(name) {
        this.name = name;
        return this;
    }

    forComponent(component) {
        this.component = component;
        return this;
    }

    withOptions(options) {
        if(!options) return this;
        Object.assign(this, options);
        return this;
    }

    /**
     * 
     * @param {Object} action 
     */
    withHooks(actions) {
        if(!actions) return this;
        Object.assign(this, actions);
        return this;
    }


    /**
     * 
     * @param {Route[]} children 
     */
    hasChildren(children) {
        this.children =  children;
        return this;       
    }
}