import Item from '../Item';

const items = [];

const tags = [
    { 
        name: 'grooming',
        items: 4
    },
    { 
        name: 'sick',
        items: 4
    },
]

tags.forEach((item) => {
    for(var i = 1; i <= item.items; i++) {
        items.push(Item.forDogs().withTag(item.name).withIndex(i));
    }
});

export { items, tags };