import Vue from 'vue'
import VueRouter from 'vue-router'
import root from './routes/root';
import rootI18n from './routes/rootI18n';

import dogs from './routes/dogs';

Vue.use(VueRouter)

const routes = [
  root,
  rootI18n.hasChildren([
    dogs
  ])
]



const router = new VueRouter({
  routes
})

export default router
