<template>
    <v-card :max-width="$vuetify.breakpoint.mdAndDown ? null : 300">
        <v-img 
            :src="imageAddress"
            class="white--text align-end"
            gradient="to bottom, rgba(0, 0, 0, .1), rgba(0, 0, 0, .5)"
            height="200px">
            <div class="d-flex align-center">
                <v-card-title class="pa-2">{{ $t('strings.track_number', { number: index }) }}</v-card-title>
                <v-btn 
                    @click="playing = !playing"
                    :class="{'ml-auto': !$vuetify.rtl, 'mr-auto': $vuetify.rtl}"
                    color="white"
                    icon>
                    <v-icon v-if="!playing">mdi-play-circle-outline</v-icon>
                    <v-icon v-else>mdi-stop-circle-outline</v-icon>
                </v-btn>
            </div>
        </v-img>
        <v-overlay absolute opacity=".8" v-if="loading">
            <div class="d-flex flex-column align-center">
                <v-progress-circular
                    size='25'
                    width='4'
                    indeterminate
                    color="primary"></v-progress-circular>
                <span class="my-2">{{$t('strings.loading_track')}}</span>
                <v-btn @click="forceStop" icon small>
                    <v-icon>mdi-close-circle-outline</v-icon>
                </v-btn>
            </div>
        </v-overlay>
    </v-card>
</template>

<script>
export default {
    computed: {
        imageAddress() {
            return require(`@/assets/media/${this.category}/${this.tag}/${this.index}.jpg`);
        },
        mediaAddress() {
            return require(`@/assets/media/${this.category}/${this.tag}/${this.index}.mp3`);
        }
    },
    data() {
        return {
            playing: false,
            loading: false,
            mediaPlayer: null
        }
    },
    methods: {
        forceStop() {
            this.loading = false;
            this.playing = false;
            if(this.mediaPlayer) {
                this.mediaPlayer.pause();
                this.mediaPlayer = null;
            }
        }
    },
    name: 'player-card',
    props: {
        category: {
            type: String,
            required: true
        },
        tag: {
            type: String,
            required: true
        },
        index: {
            type: Number,
            required: true
        }
    },
    watch: {
        playing() {
            if(this.playing) {
                this.$emit('play', {
                    tag: this.tag,
                    index: this.index
                });
                const audio = new Audio(this.mediaAddress);
                this.loading = true;
                audio.onplay = () => {
                    this.mediaPlayer = audio;
                    this.loading = false;
                };
                audio.onended = () => {
                    this.forceStop();
                }
                audio.play();
            } else {
                this.$emit('stop');
                this.forceStop();
            }
        }
    }
}
</script>

