import Route from '../models/Route';
import { i18n } from '../../localization/i18n';
import Master from '../../views/layouts/Master.vue';

const supportedLocales = process.env.VUE_APP_LOCALES.split(',');
const defaultLocale = process.env.VUE_APP_DEFAULT_LOCALE;

export default Route.withPath('/:lang').withHooks({
    beforeEnter(to, from , next) {
        if(isLangSupported(to.params.lang)) {
            i18n.locale = to.params.lang;
            next();
        } else {
            i18n.locale = defaultLocale;
            var targetPath = to.fullPath.replace(to.params.lang, defaultLocale);
            next(`${targetPath}`);
        }
    },
}).forComponent(Master);

function isLangSupported(lang) {
    return supportedLocales.includes(lang);
}